	import Vue from 'vue'
	import util from '@/libs/util'
	import {_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import monthPicker from 'c%/MonthPicker.vue'	
	
	export default{
		mixins: [common,globals],
		components: {
			monthPicker
		},
		data () {			
			return {
				data_uri: 'student_attendances',
				data: [],
				mescroll: null,
				pageSize: 10,
				currentTime: ''
			}
		},
		mounted () {
			this.pageIndex = 0	
		},
		methods: {
			hasTitle (index) {
				if(index>0){
					return String(this.data[index].int_day).substring(0,6) != String(this.data[index-1].int_day).substring(0,6)
				}
				return true
			},		
			onChange (time) {	
				this.currentTime = time
				this.data_uri = util.sprintf('student_attendances?int_day=[Between,%s,%s]',time+'01',time+'31')
				this.pageIndex = 0
				this.mescroll.resetUpScroll()
			},
			getMonth (monthIndex){
				let result = String(this.data[monthIndex].int_day)
				
				return util.sprintf('%s年%s月',
						result.substring(0,4),
						result.substring(4,6))
			},
			showMonth (int_day) {
				this.$refs['month_picker']
				.show(int_day)
			},
			status (status) {
				let map = {0: '缺勤',1: '已考勤'} 
				return map[parseInt(status)]
			},
			getClass (status) {
				return `color-status-${status}`
			},
	        deal_data (response) {
	        	let list = response.body.data.list,	
                	total = response.body.data.total,
                	curLength = 0
                	
                
                if(this.data_uri.indexOf('int_day')>-1&&list.length==0){
                	let currentTime = this.currentTime
                	let title = util.sprintf('%s年%s月',
								currentTime.substring(0,4),
								currentTime.substring(4,6))
                	this.$vux.alert.show({
					  title: '提示',
					  content: title+'没有考勤记录~'					  
					})
                	
                	this.mescroll.endBySize(this.data.length, this.data.length)
                	return 
                }	
                
            	if(this.pageIndex===1){
	        		this.data = []
	        	}
	        	
	    		for(let l in list){	    			
	    			this.data = this.data.concat(list[l])	
	    			curLength += list[l].length
		        }
	    		
	    		this.mescroll.endBySize(curLength, total)
    		}
		}
	}



